import React, { useState } from 'react';
import TicketCreator, { HashedTicket } from './components/TicketCreator';
import Distributor from './components/Distributor';
import Buyer from './components/Buyer';
import EventGate from './components/EventGate';
import { Transaction, PrivateKey, PublicKey } from '@bsv/sdk'

const App: React.FC = () => {
  const [hashedTickets] = useState<HashedTicket[] | null>(null);
  const [creatorKey, setCreatorKey] = useState<PrivateKey | null>(null);
  const [hmacKey, setHmacKey] = useState<string | null>(null);
  const [distributedTx, setDistributedTx] = useState<Transaction | null>(null);
  const [distributorKey, setDistributorKey] = useState<PrivateKey | null>(null);
  const [boughtTx, setBoughtTx] = useState<Transaction | null>(null);
  const [buyerKey, setBuyerKey] = useState<PrivateKey | null>(null);
  const [buyerPublicKey, setBuyerPublicKey] = useState<PublicKey | null>(null);

  return (
    <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gridTemplateRows: '1fr 1fr', gap: '10px' }}>
      <div>
        <TicketCreator onCreate={(privateKey, hmacKey) => {
          setCreatorKey(privateKey);
          setHmacKey(hmacKey);
        }} />
      </div>
      <div>
        <Distributor hashedTickets={hashedTickets || []} creatorKey={creatorKey} hmacKey={hmacKey} onDistribute={(tx, privateKey) => {
          setDistributedTx(tx);
          setDistributorKey(privateKey);
        }} />
      </div>
      <div>
        <EventGate boughtTx={boughtTx} buyerKey={buyerKey} onEventGateEntry={(tx, privateKey) => {
          setBoughtTx(tx);
          setBuyerKey(privateKey);
        }} />
      </div>
      <div>
        <Buyer distributedTx={distributedTx} distributorKey={distributorKey} buyerPublicKey={buyerPublicKey} onBuy={(tx, privateKey, publicKey) => {
          setBuyerPublicKey(publicKey);
          setBoughtTx(tx);
          setBuyerKey(privateKey);
        }} />
      </div>
    </div>
  );
};
export default App;