import React, { useState } from 'react';
import { Hash, Transaction, PrivateKey, PublicKey, P2PKH  } from '@bsv/sdk';

interface Props {
  distributedTx: Transaction | null;
  distributorKey: PrivateKey | null;
  buyerPublicKey: PublicKey | null;
  onBuy: (tx: Transaction, privateKey: PrivateKey, publicKey: PublicKey) => void;
}

const Buyer: React.FC<Props> = ({ distributedTx, distributorKey, buyerPublicKey, onBuy }) => {
  const [privateKey] = useState<PrivateKey>(new PrivateKey());
  const [publicKey] = useState<PublicKey>(privateKey.toPublicKey);
  const [boughtTickets, setBoughtTickets] = useState<{ hash: number[] }[]>([]);

  const buyTicket = () => {
    if (!distributedTx || !distributorKey) return;

    const ticketHash = Hash.sha256(
      distributedTx.outputs[0].lockingScript.toASM()
    );

    const newTx = new Transaction();

    newTx.addInput({
      sourceTransaction: distributedTx,
      sourceOutputIndex: 0,
      unlockingScriptTemplate: new P2PKH().unlock(distributorKey),
      sequence: 0xFFFFFFFF
    });

    newTx.addOutput({
      change: false,
      lockingScript: new P2PKH().lock(buyerPublicKey!.toAddress()),
      satoshis: 3,
    });

    newTx.sign();

    setBoughtTickets([...boughtTickets, { hash: ticketHash }]);
    onBuy(newTx, privateKey, publicKey);
  };

  return (
    <div>
      <h2>Buyer</h2>
      <button onClick={buyTicket}>Buy Ticket</button>
      <ul>
        {boughtTickets.map((t, idx) => (
          <li key={idx}>{t.hash.toString()}</li>
        ))}
      </ul>
    </div>
  );
};

export default Buyer;
