import React, { useState } from 'react';
import { Transaction, PrivateKey, PublicKey, Hash, P2PKH } from '@bsv/sdk'
import { Ticket } from './TicketCreator';

interface HashedTicket {
  ticket: Ticket;
  hash: number[];
}


interface Props {
  hashedTickets: HashedTicket[];
  creatorKey: PrivateKey | null;
  hmacKey: string | null;
  onDistribute: (tx: Transaction, privateKey: PrivateKey) => void;
}

const Distributor: React.FC<Props> = ({ hashedTickets, creatorKey, hmacKey, onDistribute }) => {
  const [privateKey] = useState<PrivateKey>(new PrivateKey());
  const [publicKey] = useState<PublicKey>(privateKey.toPublicKey);
  const [distributedTickets, setDistributedTickets] = useState<HashedTicket[]>([]);

  const distributeTickets = () => {
    if (!creatorKey || !hmacKey) return;

    const tx = new Transaction();
    
    tx.addInput({
      sourceTransaction: tx,
      sourceOutputIndex: 0,
      unlockingScriptTemplate: new P2PKH().unlock(creatorKey),
      sequence: 0xFFFFFFFF,
    });

    hashedTickets.forEach(t => {

      tx.addOutput({
        lockingScript: new P2PKH().lock(Hash.sha256hmac(hmacKey, Hash.sha256(publicKey.toString()+t.toString()))),
        satoshis: 3,
      })
    });

    tx.sign();
    setDistributedTickets(hashedTickets);
    onDistribute(tx, privateKey);
  };

  return (
    <div>
      <h2>Distributor</h2>
      <button onClick={distributeTickets}>Distribute Tickets</button>
      <ul>
        {distributedTickets.map((t, idx) => (
          <li key={idx}>{JSON.stringify(t.ticket)}</li>
        ))}
      </ul>
    </div>
  );
};

export default Distributor;