import React, { useState } from 'react';
import { Transaction, PrivateKey, PublicKey, P2PKH } from '@bsv/sdk';

interface Props {
  boughtTx: Transaction | null;
  buyerKey: PrivateKey | null;
  onEventGateEntry: (tx: Transaction, privateKey: PrivateKey, publicKey: PublicKey) => void;
}

const EventGate: React.FC<Props> = ({ boughtTx, buyerKey }) => {
  const [privateKey] = useState<PrivateKey>(new PrivateKey());
  const [publicKey] = useState<PublicKey>(privateKey.toPublicKey);
  const [redeemedTickets, setRedeemedTickets] = useState<{ hash: string }[]>([]);

  const redeemTicket = () => {
    if (!boughtTx || !buyerKey) return;

    const ticketHash = boughtTx.outputs[0].lockingScript.toASM();

    const newTx = new Transaction()

    newTx.addInput({
      sourceTransaction: boughtTx,
      sourceOutputIndex: 0,
      unlockingScriptTemplate: new P2PKH().unlock(buyerKey),
      sequence: 0xFFFFFFFF,
    });

    newTx.addOutput({
      lockingScript: new P2PKH().lock(publicKey.toAddress()),
      satoshis: 3
    });

    setRedeemedTickets([{ hash: ticketHash }]);

  };

  return (
    <div>
      <h2>Event Gate</h2>
      <button onClick={redeemTicket}>Redeem Ticket</button>
      <ul>
        {redeemedTickets.map((t, idx) => (
          <li key={idx}>{t.hash.toString()}</li>
        ))}
      </ul>
    </div>
  );
};

export default EventGate;