import React, { useState } from 'react';
import { Hash, PrivateKey } from '@bsv/sdk';


interface Props {
    onCreate: (privateKey: PrivateKey, hmacKey: string) => void;
  }
  
export interface Ticket {
    eventName: string;
    section: string;
    row: string;
    seat: string;
}
export interface HashedTicket {
  ticket: Ticket;
  hash: number[];
}

  
  const TicketCreator: React.FC<Props> = ({ onCreate }) => {
    const [eventName, setEvent] = useState('');
    const [section, setSection] = useState('');
    const [row, setRow] = useState('');
    const [seat, setSeat] = useState('');
    const [tickets, setTickets] = useState<Ticket[]>([]);
    const [hashedTickets, setHashedTickets] = useState<HashedTicket[]>([]);
    const [privateKey] = useState<PrivateKey>(new PrivateKey());
    const [hmacKey] = useState<string>(PrivateKey.fromRandom().toString());
  
    const handleAddTicket = () => {
      const newTicket: Ticket = { eventName, section, row, seat };
      setTickets([...tickets, newTicket]);
      setEvent('');
      setSection('');
      setRow('');
      setSeat('');
    };
  
    const handleCreateTickets = () => {
      const newHashedTickets: HashedTicket[] = tickets.map(ticket => ({
        ticket,
        hash: Hash.sha256hmac(hmacKey,(JSON.stringify(ticket))),
      }));
      setHashedTickets(hashedTickets.concat(newHashedTickets));
    };
  
      onCreate(privateKey, hmacKey);
  
    return (
      <div>
        <h2>Ticket Creator</h2>
        <div>
          <input
            type="text"
            placeholder="Event Name"
            value={eventName}
            onChange={e => setEvent(e.target.value)}
          />
          <input
            type="text"
            placeholder="Section"
            value={section}
            onChange={e => setSection(e.target.value)}
          />
          <input
            type="text"
            placeholder="Row"
            value={row}
            onChange={e => setRow(e.target.value)}
          />
          <input
            type="text"
            placeholder="Seat"
            value={seat}
            onChange={e => setSeat(e.target.value)}
          />
          <button onClick={handleAddTicket}>Add Ticket</button>
        </div>
        <button onClick={handleCreateTickets}>Create Tickets</button>
        <ul>
          {tickets.map((ticket, idx) => (
            <li key={idx}>{JSON.stringify(ticket)}</li>
          ))}
        </ul>
      </div>
    );
  };
  
  export default TicketCreator;